import React, { Component } from 'react'
import axios from 'axios'
import { isEqual, cloneDeep } from 'lodash'
import BuilderElement from './BuilderElement/BuilderElement'
import { renderToString } from 'react-dom/server'

if (process.env.NODE_ENV === 'development') {
    axios.defaults.baseURL = 'http://api.websiter.dev:5000'
} else {
    axios.defaults.baseURL = 'https://api.websiter.dev'
}

axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.headers.put['Content-Type'] = 'application/json'
axios.defaults.headers.delete['Content-Type'] = 'application/json'
axios.defaults.headers.common.Accept = 'application/json'

class App extends Component {
    state = {
        resourcesObjects: {},
        page: '',
        structure: [],
        pagesStructure: [],
        pluginsStructure: [],
        baseUrl: '',
    }

    componentDidMount() {
        return axios
            .post(
                '/api/resources/live',
                JSON.stringify({
                    url: window.location.href,
                })
            )
            .then(response => {
                this.setState({
                    ...response.data,
                    structure:
                        response.data.resourcesObjects[response.data.page]
                            .structure,
                })
            })
            .catch(err => {})
    }

    render() {
        const currentPageItemInStructure = this.state.pagesStructure.find(
            item => item.id === this.state.page
        )
        if (this.state.structure.length > 0) {
            const bodyElement = this.state.structure.filter(
                item => item.path.length === 1
            )[1]
            const bodyProps = cloneDeep(bodyElement.properties)
            const bodyStyle = bodyElement.style
            if (bodyStyle) bodyProps.style = bodyStyle
            for (let attr in bodyProps) {
                document.body.setAttribute(attr, bodyProps[attr])
            }

            const htmlElement = this.state.structure.filter(
                item => item.path.length === 0
            )[0]
            const htmlProps = cloneDeep(htmlElement.properties)
            const htmlStyle = htmlElement.style
            if (htmlStyle) htmlProps.style = htmlStyle
            for (let attr in htmlProps) {
                document.documentElement.setAttribute(attr, htmlProps[attr])
            }

            return this.state.structure
                .filter(itemInn => isEqual(itemInn.path, ['element_-1']))
                .map((itemInn, index) => {
                    const result = (
                        <BuilderElement
                            key={itemInn.id}
                            pagesStructure={this.state.pagesStructure}
                            structure={this.state.structure}
                            element={itemInn}
                            pluginsStructure={this.state.pluginsStructure}
                            resourcesObjects={this.state.resourcesObjects}
                            resourceDraft={
                                this.state.page
                                    ? this.state.resourcesObjects[
                                          this.state.page
                                      ]
                                    : {}
                            }
                            currentResource={this.state.page}
                            baseUrl={this.state.baseUrl}
                            isHead={index === 0}
                            isHeadOrBody={true}
                            pluginsPathArray={[]}
                            pageInStructure={currentPageItemInStructure}
                        />
                    )
                    if (index === 0) {
                        document.head.innerHTML =
                            document.head.innerHTML + renderToString(result)
                    } else {
                        return result
                    }
                })
        } else {
            return null
        }
    }
}

export default App
